/* ==========================================================================
   Header Standard style - begin
   ========================================================================== */

.edgt-header-standard {

    &.edgt-boxed{
        .edgt-vertical-align-containers {
            padding: 0 43px;
        }
    }
	
	.edgt-menu-area {
		
		&.edgt-menu-left {
			
			.edgt-main-menu {
				width: auto;
				padding: 0 0 0 20px;
			}
			
			.edgt-vertical-align-containers {
				
				.edgt-position-left-inner {
					height: 100%;
				}
			}
		}
		
		&.edgt-menu-center {
			
			.edgt-vertical-align-containers {
				
				.edgt-position-center-inner {
					height: 100%;
				}
			}
		}
		
		&.edgt-menu-right {
			
			.edgt-main-menu {
				width: auto;
			}
			
			.edgt-vertical-align-containers {
				
				.edgt-position-right-inner {
					height: 100%;
				}
			}
		}
	}
}
/* ==========================================================================
   Header Standard style - end
   ========================================================================== */