/* ==========================================================================
   Header Minimal style - begin
   ========================================================================== */

.edgt-header-minimal {
	
    .edgt-page-header {
	    
	    .edgt-menu-area {
		
		    .edgt-position-right-inner {
			    height: auto;
		    }
	    }
	
	    .edgt-position-left:before,
	    .edgt-position-right:before {
		    content: "";
	    }
	
	    .edgt-sticky-header {
		
		    .edgt-position-left-inner {
			    height: auto;
		    }
		
		    .edgt-vertical-align-containers .edgt-position-right .edgt-position-right-inner {
			    height: auto;
		    }
	    }
    }
}
/* ==========================================================================
   Header Minimal style - end
   ========================================================================== */