/* ==========================================================================
#Mobile header styles
========================================================================== */
.edgt-mobile-header {
	display: none;
	position: relative;
	z-index: 111; /* one more thatn the content */

	.edgt-mobile-side-area {
		background-color: #fff;
		background-size: cover;
		height: 100%;
		min-height: 100%;
		overflow: hidden;
		padding: 20% 70px;
		box-sizing: border-box;
		position: fixed;
		top: 0;
		right: 0;
		width: 420px;
		z-index: 9999;
		-webkit-backface-visibility: hidden;
		@include edgtTransform(translate3d(100%, 0,0));
		@include edgtTransition(transform .7s cubic-bezier(0.25,1,.2,1));

		.edgt-social-icon-widget-holder {
			.edgt-social-icon-widget {
				color: #fff;
				font-size: 15px;
			}
		}

		.widget_text {
			margin-bottom: 5px;
		}

		&.opened{
			@include edgtTransform(translate3d(0,0,0));
            border-left: 1px solid #ebebeb;

			.edgt-mobile-nav,
			.edgt-mobile-menu-bottom {
				@include edgtTransform(translateX(0px));
				opacity: 1;
			}
		}

		.edgt-mobile-side-area-inner{
			height: 100%;
			min-height: 100%;
			overflow: hidden;

		}

		.edgt-close-mobile-side-area-holder {
			display: block;
			position: absolute;
			top: 23px;
			right: 24px;
			z-index: 1000;
			cursor: pointer;

			span {
				color: #92919c;
				display: inline-block;
				font-size: 24px;
				height: 24px;
				width: 24px;
				line-height: 24px;
				position: relative;
				text-align: center;
			}
		}

		.edgt-mobile-menu-bottom {
			color: #fff;
			.textwidget {
				color: #a5a5a5;
				font-size: 12px;
				letter-spacing: 0.1em;
			}
		}
	}

	/* #General mobile header styles
    ========================================================================== */
	.edgt-mobile-header-inner {
		position: relative;
		height: 100px;
		background-color: #fff;
		box-sizing: border-box;
        border-bottom: 1px solid rgb(225, 225, 225);

		.edgt-mobile-header-holder {
			position: relative;
			z-index: 2;
			height: 100%;
		}
	}

	.edgt-grid {
		height: 100%;
	}

	.edgt-vertical-align-containers {
		padding: 0;
	}

	/* #End of general mobile header styles
   ========================================================================== */

	/* #Mobile logo styles
    ========================================================================== */
	.edgt-mobile-logo-wrapper a {
		display: block;
	}

	.edgt-mobile-logo-wrapper img {
		height: 100%;
		width: auto;
	}
	/* #End of mobile logo styles
    ========================================================================== */

	/* #Mobile menu opener styles
    ========================================================================== */
	.edgt-mobile-menu-opener {
		float: left;
		position: relative;
		z-index: 2;
		height: 100%;

		a {
			display: table;
			height: 100%;
			padding-right: 10px;
			font-size: 22px;

			.edgt-mo-lines {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				width: 31px;
				height: 17px;

				.edgt-mo-line {
					position: absolute;
					left: 0;
					height: 1px;
					width: 100%;
					background-color: $default-heading-color;
					-webkit-transition: -webkit-transform .3s ease, top .2s ease-out, background-color .2s ease-out;
					-moz-transition: -moz-transform .3s ease, top .2s ease-out, background-color .2s ease-out;
					transition: transform .3s ease, top .2s ease-out, background-color .2s ease-out;

					&:before {
						position: absolute;
						top: -1px;
						left: 0;
						display: block;
						width: 100%;
						height: 1px;
						background-color: inherit;
						opacity: 0.5;
						content: '';
					}

					&.edgt-line-1 {
						top: 0;
					}

					&.edgt-line-2 {
						top: calc(50% - 1px);
						top: -moz-calc(50%); //moz pixel move fix
						outline:1px solid transparent; //moz pixelation fix
					}

					&.edgt-line-3 {
						top: calc(100% - 1px);
					}
				}
			}

			.edgt-mobile-menu-text {
				position: relative;
				top: 0;
				margin: 0;
				padding-left: 3px;
			}

			&:hover {

				.edgt-mo-lines .edgt-mo-line {

					&.edgt-line-1,
					&.edgt-line-3 {
						top:50%;
					}

					&.edgt-line-2 {
						@include edgtTransform(rotate(90deg));
					}
				}
			}
		}

		.edgt-mo-icon-holder {
			display: table-cell;
			vertical-align: middle;
			line-height: 1em;

			> * {
				display: inline-block;
				vertical-align: middle;
			}
		}
	}
	/* #End of mobile menu opener styles
    ========================================================================== */
}

/* ==========================================================================
#End of mobile header styles
========================================================================== */

/* ==========================================================================
#Sticky mobile header styles
========================================================================== */
.edgt-sticky-up-mobile-header {
	.edgt-animate-mobile-header {
		.edgt-mobile-header-inner {
			width: 100%;

			//Added left and right transition values because of sidearea transitions on open/close
			-webkit-transition: -webkit-transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
			-moz-transition: -moz-transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
			-o-transition: -o-transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
			transition: transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
			@include edgtTransform(translateY(-100%));
		}
	}

	.mobile-header-appear {
		.edgt-mobile-header-inner {
			top: 0;
			left: 0;
			width: 100%;
			position: fixed;
			@include edgtTransform(translateY(0));
		}
	}
}
/* ==========================================================================
#End of sticky mobile header styles
========================================================================== */

.edgt-mobile-header {

	.edgt-mobile-nav {

		ul {
			padding: 0;
			margin: 0;
			list-style-type: none;

			li {
				position: relative;
				margin: 0;
				padding: 0;
				font-size: 13px;
				text-transform: uppercase;

				&.current-menu-item > a,
				&.current-menu-ancestor > a {
					color: $first-main-color;
				}

				&.edgt-opened {
				}

				&.edgt-opened > a {
				}

				a, h6 { //h6 is here because of no link option in navigation
					position: relative;
					display: inline-block;
					padding: 12px 0;
					margin: 0;
                    color: #b9b9b9;
					font-size: 23px;
					font-weight: 400;

					&:hover {
						color: $first-main-color;
					}
				}
			}

			//dropdowns
			ul {
				display: none;
				padding: 10px 0;

				li {

					&.current-menu-ancestor > a,
					&.current-menu-item > a {
						color: $first-main-color;
					}

					.mobile_arrow {
						top: 1px;
						color: #b9b9b9;
					}

					&.edgt-opened > a {
					}

					a, h6 {
						font-size: 16px;
						line-height: 25px;
						text-transform: initial;
						padding: 3px 0;
					}
				}

				ul {
					li {
						a {
							font-size: 15px;
						}
					}
				}
			}
		}

		.edgt-grid > ul {

			> li {

				> a,
				> h6 {
					font-size: 22px;
                    font-weight: 700;
                    color: #353535;

					&:hover {
						color: $first-main-color;
					}
				}

				&.edgt-active-item > a {
					opacity: .8;
					color: $first-main-color;
				}

				&.edgt-active-item > .mobile_arrow > .edgt-sub-arrow {
					@include edgtTransform(rotate(90deg));
					color: $first-main-color;
				}
			}
		}

		li.current-menu-ancestor > .mobile_arrow,
		li.current-menu-item .mobile_arrow {
			color: $first-main-color;
		}

		.mobile_arrow {
			$mobile-arrow-height: 30px;

			position: relative;
			right: 0;
			top: -2px;
			width: 50px;
			height: $mobile-arrow-height;
			line-height: $mobile-arrow-height;
			font-size: 14px;
			text-align: right;
            color: #353535;
			padding-left: 3px;

			&:hover {
				cursor: pointer;
			}

			.edgt-sub-arrow {
				@include edgtTransition(transform 0.2s ease-in-out);
				display: inline-block;
			}

			//@TODO remove this line of code when new headers are enabled
			.arrow_triangle-down {
				display: none; //temporarily
			}
		}

		li.edgt-opened > .mobile_arrow > .edgt-sub-arrow {
			@include edgtTransform(rotate(90deg));
		}

		& > ul > li:last-child > a,
		& > .edgt-grid > ul > li:last-child > a {
			border-bottom: none;
		}
	}
}