/* ==========================================================================
   Fixed header style - begin
   ========================================================================== */

.edgt-page-header {
    
    .edgt-fixed-wrapper {
        
        &.fixed {
            position: fixed;
	        top: 0;
	        left: 0;
	        width: 100%;
            -webkit-backface-visibility: hidden;
            border-bottom: 1px solid #e5e5e5;
            
            .admin-bar & {
                margin-top: 32px;
            }
	
	        .edgt-menu-area {
		        background-color: #fff;
	        }
        }
	
	    .edgt-fixed-on-scroll & {
		
		    .edgt-menu-area {
			    @include edgtTransition(height .2s ease);
		    }
	    }
    }
}
/* ==========================================================================
   Fixed header style - end
   ========================================================================== */