/* ==========================================================================
   Top header style - begin
   ========================================================================== */

.edgt-top-bar {
	@include edgtRelativeHolderLayout();
	height: 50px;
	color: #212121;
	font-size: 12px;
	font-weight: 400;
	line-height: 1;
	background-color: #ffffff;
	z-index: 111;
	border-bottom: 1px solid #e1e1e1;

	> .edgt-vertical-align-containers {
		padding: 0 35px;
		.edgt-position-right-inner {
			height: 100%;
		}
	}
	
	.edgt-grid {
		height: 100%;
		
		.edgt-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;
			
			.edgt-top-bar-in-grid-padding & {
				padding: 0 60px;
			}
		}
	}
	
	.widget {
		margin: 0;
	}
}
/* ==========================================================================
   Top header style - end
   ========================================================================== */